'use client';
import React, { useState } from 'react';
import { 
  FiHome, 
  FiUser, 
  FiFileText, 
  FiImage, 
  FiLayers, 
  FiMail,
  FiTwitter,
  FiLinkedin,
  FiInstagram,
  FiGithub,
  FiMoon,
  FiSun
} from 'react-icons/fi';
import { useTheme } from '@/contexts/ThemeContext';

export default function Sidebar() {
  const { theme, toggleTheme } = useTheme();
  const [isPortuguese, setIsPortuguese] = useState(true);

  const menuItems = [
    { name: 'Home', href: '#home', icon: <FiHome size={24} /> },
    { name: 'About', href: '#about', icon: <FiUser size={24} /> },
    { name: 'Resume', href: '#resume', icon: <FiFileText size={24} /> },
    { name: 'Portfolio', href: '#portfolio', icon: <FiImage size={24} /> },
    { name: 'Services', href: '#services', icon: <FiLayers size={24} /> },
    { name: 'Contact', href: '#contact', icon: <FiMail size={24} /> },
  ];

  const socialLinks = [
    { name: 'Twitter', href: '#', icon: <FiTwitter size={16} /> },
    { name: 'LinkedIn', href: '#', icon: <FiLinkedin size={16} /> },
    { name: 'Instagram', href: '#', icon: <FiInstagram size={16} /> },
    { name: 'Github', href: '#', icon: <FiGithub size={16} /> },
  ];

  return (
    <div className="fixed left-6 top-20 bottom-20 w-64 bg-sidebar dark:bg-sidebar-dark backdrop-blur-sm rounded-2xl border border-gray-300 dark:border-gray-700/50 flex flex-col p-4 shadow-2xl">
      
      {/* Controls - Dark/Light e PT/EN */}
      <div className="flex justify-between items-center mb-6 px-2">
        {/* Dark/Light Toggle */}
        <div className="flex items-center space-x-2">
          <FiMoon size={16} className="text-gray-600 dark:text-gray-400" />
          <button
            onClick={toggleTheme}
            className={`w-12 h-6 flex items-center rounded-full p-1 transition-all duration-300 ${
              theme === 'dark' ? 'bg-blue-600 justify-end' : 'bg-gray-400 justify-start'
            }`}
          >
            <div className="w-4 h-4 bg-white rounded-full shadow-md"></div>
          </button>
          <FiSun size={16} className="text-gray-600 dark:text-gray-400" />
        </div>

        {/* Language Toggle */}
        <button
          onClick={() => setIsPortuguese(!isPortuguese)}
          className={`px-3 py-1 rounded-full text-sm font-medium transition-all duration-200 ${
            isPortuguese 
              ? 'bg-blue-600 text-white' 
              : 'bg-gray-300 dark:bg-gray-700 text-gray-700 dark:text-gray-300 hover:bg-gray-400 dark:hover:bg-gray-600'
          }`}
        >
          {isPortuguese ? 'PT' : 'EN'}
        </button>
      </div>

      {/* Menu Navigation */}
      <nav className="flex-1 flex flex-col justify-center">
        <ul className="space-y-2">
          {menuItems.map((item) => (
            <li key={item.href}>
              <a
                href={item.href}
                className="flex items-center py-2 px-3 text-gray-700 dark:text-gray-300 hover:bg-blue-600 hover:text-white rounded-xl transition-all duration-200 group"
              >
                <span className="mr-3 text-gray-500 dark:text-gray-400 group-hover:text-white">
                  {item.icon}
                </span>
                <span className="font-medium text-sm">{item.name}</span>
              </a>
            </li>
          ))}
        </ul>
      </nav>

      {/* Social Links */}
      <div className="mt-auto">
        <div className="flex justify-center space-x-3">
          {socialLinks.map((social) => (
            <a
              key={social.name}
              href={social.href}
              className="w-8 h-8 flex items-center justify-center bg-gray-200 dark:bg-gray-800/80 text-gray-600 dark:text-gray-400 rounded-lg hover:bg-blue-600 hover:text-white transition-all duration-200"
              title={social.name}
            >
              {social.icon}
            </a>
          ))}
        </div>
      </div>
    </div>
  );
}